import Link from "next/link";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { amenities } from "@/lib/amenities";
// STEP 1: slug generator
function slugify(text: string) {
  return text
    .toLowerCase()
    .replace(/[^a-z0-9\s-]/g, "")
    .trim()
    .replace(/\s+/g, "-");
}

// STEP 2: amenities array WITH slugs
amenities.map((item) => ({
  ...item,
  slug: slugify(item.name),
}));

// STEP 3: UI with clickable cards
export default function Amenities() {
  return (
    <section id="amenities" className="py-16 md:py-24 bg-muted">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center mb-12">
          <h2 className="text-3xl md:text-4xl font-bold mb-4">World-Class Amenities</h2>
          <p className="text-muted-foreground text-lg">
            Everything you need for an unforgettable stay
          </p>
        </div>

        <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
          {amenities.map((amenity) => (
            <Link key={amenity.slug} href={`/amenities/${amenity.slug}`}>
              <Card className="cursor-pointer hover:shadow-lg transition">
                <CardHeader>
                  <CardTitle className="flex items-center gap-3">
                    <span className="text-3xl">{amenity.icon}</span>
                    {amenity.name}
                  </CardTitle>
                </CardHeader>

                <CardContent>
                  <p className="text-muted-foreground">{amenity.description}</p>
                </CardContent>
              </Card>
            </Link>
          ))}
        </div>
      </div>
    </section>
  );
}

// export array so slug page can read it
export { amenities };
